.Navbar_navbar__3fy7q{
    height: 10vh;
    padding:1vw 3vw;
    display: flex;
    align-items: center;
    background-color: #070707;
    border: 1px solid black;
}

.Navbar_links__KyZMd{
    text-decoration: none;
    color: white;
    margin: 0 1vw;
    position: relative;
    transition-duration: 300ms;
}

.Navbar_links__KyZMd::after{
    content: '';
    width: 0%;
    height: 3px;
    background-color: goldenrod;
    position: absolute;
    left: 50%;
    bottom: 0%;
    border-radius: 10px;
    transition: all 500ms ease;
}

.Navbar_links__KyZMd:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.Navbar_links__KyZMd:active::after{
    width: 100%;
    left: 0;
}
.Navbar_links__KyZMd:hover::after{
    width: 100%;
    left: 0;
    bottom: 2%;
}


